import React from 'react';
import userImg from '../../../assets/images/user.svg'
import Moment from 'react-moment';

const Message = (props) => {
    let colorMessage = {
        color: props.message.color
    };
    let colorBg = {
        backgroundColor :  props.message.color
    };
    let data = props.message.message.created_at;

    console.log(data);
    return (
        <div className="message">
            <div className="avatar" style={colorBg}>
                <img src={props.message.message.member.avatar || userImg} />
            </div>

            <div className="data">
                <div className="head-message">
                    <p className="username" style={colorMessage}>
                      {props.message.message.member.username}
                    </p>
                    <span className="time-label">
                        <Moment  title={data} parse="YYYY-MM-DD HH:mm" format="hh:mm"> {data} </Moment>
                    </span>
                </div>
              {props.message.message.type === 'text' ? (
                <div className="text">
                  {props.message.message.body}
                </div>
              ) : (
                <div className="image">
                  <img src={props.message.message.url} alt=""/>
                </div>
              )}
            </div>
        </div>
    )
};

export default Message;
