import React, { Component } from 'react';
import Message from "./Message";
import MessageLoad from "./MessageLoad";

class MessagesList extends Component {

    constructor(props){
        super(props);

        this.state = {
            height : 0,
            messages : props.messages,
            loadMessagesCheck:false
        }

    }

    static getDerivedStateFromProps(nextProps, prevState){
        return  {
            messages : nextProps.messages,
        }
    }

    componentDidMount(){
        this.assignHeight();
        window.addEventListener("resize",this.assignHeight.bind(this));
        this.setState({
            loadMessagesCheck : true
        })

    }

    assignHeight(){
        let widgetHeight =  document.getElementById('chat-widget-root').offsetHeight;
        let _docHeight = (widgetHeight !== undefined) ? widgetHeight : document.body.offsetHeight;

        this.setState({
            height : _docHeight - 93
        });
    }

    componentWillUnmount(){
        window.removeEventListener("resize", this.assignHeight.bind(this));
    }

    render() {

        let { messages, messagesLoad } = this.props;

        let messagesList = '';
        let messagesListLoad = 'No messages in chat room';

        if (messages.length) {
            messagesList = messages.map(message => {
                return <Message key={message.message.id} message={message}   />
            });
        }
        if (messagesLoad.length) {
            messagesListLoad = messagesLoad.map((message, index )=> {
                // console.log(message[index].message.id);
               return <MessageLoad  messagesLoad={message}   />;

            });

        }

        return (
            <div className="chat-widget-messages" style={{height : this.state.height + 'px'}}>

                {!!this.state.loadMessagesCheck &&
                    <div className="loadMessages">
                        {messagesListLoad}
                    </div>
                }

                {messagesList}
            </div>
        )
    }
}

export default MessagesList;
