import React from 'react';
import MessagesList from "./Messages/MessagesList";
import ChatMessageBox from './ChatMessageBox/ChatMessageBox';
import socketIOClient from 'socket.io-client';
import * as axios  from 'axios';
import Navbar from "../Navbar";
import EnterChat from "./EnterChat/EnterChat";
import {socketConnection} from "../../api/Authorization";

class Chat extends React.Component {

    constructor(props){
        super(props);

        this.socket = null;

        this.state = {
            username :null,
            token : localStorage.getItem('token') ? localStorage.getItem('token') : '',
            chat_ready : false,
            avatar: null,
            user_id: null,
            users : [],
            messages : [],
            messagesLoad: [],
            message : '',
            color : null,
            setOffset : 0,
            setLimit :30,
            loadMessagesCheck:false
        }

    }

    componentDidMount(){
        window.addEventListener('scroll', this.handleScroll);
        if(this.state.token.length) {
            this.initChat();
        }
    }
    componentWillUnmount(){
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll =(event) => {
       let setOffset = this.state.setLimit;
       let messagesBlock = document.getElementsByClassName('chat-widget-messages')[0];

       if(messagesBlock.scrollTop === 0 && !!this.state.loadMessagesCheck){
           this.loadMessage(setOffset, this.state.setLimit);
       }

    }

    setToken(token, e){
        this.setState({
            token : token
        }, () => {
            this.initChat();
        });
    }

    addMessage(message){
        const color = (message.owner_id === this.state.user_id) ? "#08E8C8" : "#FF8C82";

        const formattedMessages = [
            ...this.state.messages,
            {
                message : message,
                color : color
            }
        ];

        this.setState({
            messages : formattedMessages
        });
        this.scrollToBottom();
    }

    sendMessage(message){

        let data = new FormData();
        data.append('type', "text");
        data.append('body', message);


        axios.post(`https://devapi.flookschat.tk/api/chat/main/messages`, data, {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }).then((res) => {

        });

    }

    scrollToBottom(scrollTo){
        let messagesInput = document.getElementsByClassName('chat-widget-messages')[0];

        if(scrollTo){
            messagesInput.scrollTop = messagesInput.getElementsByClassName('loadMessages')[0].lastChild.scrollHeight;
        }else{
            messagesInput.scrollTop = messagesInput.scrollHeight - messagesInput.clientHeight;
        }

    }

    loadMessage(setOffset, setLimit){
        let getMessages;
        let countOffset = this.state.setOffset;

        axios.get('https://devapi.flookschat.tk/api/chat/main/messages', { params: { offset: (this.state.setOffset || setOffset), limit: (setLimit || this.state.setLimit)}}).then((res) => {
            getMessages = res.data;
                const Messages = [
                    ...this.state.messagesLoad,
                    getMessages.map(message => {

                        let color = (message.owner_id === this.state.user_id) ? "#08E8C8" : "#FF8C82";

                        return ({
                            message: message,
                            color: color
                        })
                    })
                ];


                if(getMessages.length){
                    countOffset = countOffset + 30;

                    this.setState({
                        messagesLoad: Messages,
                        setOffset: countOffset,
                        loadMessagesCheck: true
                    })
                    ;
                    if (countOffset <= 30) {
                        this.scrollToBottom();
                    } else {
                        this.scrollToBottom(true);
                    }
                }else{
                    this.setState({
                        loadMessagesCheck: false
                    })
                }
                console.log(this.state.loadMessagesCheck);
        });
    }

    initChat(){
        this.setState({
            chat_ready : true
        });

        this.loadMessage();

        let user;
        // this.socket = socketConnection;

        axios.defaults.headers.common['Authorization'] = 'FLOOKS ' + this.state.token;

        this.socket = socketIOClient.connect('wss://devapi.flookschat.tk:6001', {
            transports: ['websocket'],
            query: {
                token: this.state.token
            }
        });

        axios.get('https://devapi.flookschat.tk/api/user/current').then((res) => {
            user = res.data;

            if (user.id) {
                console.log(user);

                this.setState({
                    user_id: user.id
                });

                localStorage.setItem('token', this.state.token);

                this.socket.emit('subscribe', `user:${user.id}`);

                this.socket.on(`user:${user.id}:new-conversation`, (data) => {
                    console.log('new-conversation', data);
                });

                this.socket.on(`user:${user.id}:message`, (data) => {
                    console.log(data);
                     this.addMessage(data);
                });
            } else {
                alert('User not found');
            }
        });

        let channel =  `chat:main`;

        this.socket.on('connect', () => {
            this.socket.emit('subscribe', channel);
            if (user) {
                this.socket.emit('subscribe', `user:${user.id}`);
            }
        });

        this.socket.on(channel + ':message', (data) => {
            // console.log(data);
            if (channel !== 'chat:main') {
                this.socket.emit('last_seen_conversation', {
                    user_id: user.id,
                    conversation_id: data.conversation_id,
                });
            }
             this.addMessage(data);
        });

    }

    render() {
        return (
            <div className="chat-widget" onScroll={this.handleScroll} >
                {this.state.chat_ready ? (
                    <React.Fragment>
                        <Navbar/>
                        <div className="chat-widget-wrapper-message">
                            <MessagesList
                                messages={this.state.messages}
                                messagesLoad={this.state.messagesLoad}
                            />
                            <ChatMessageBox
                                sendMessage={this.sendMessage.bind(this)}
                                type="main"
                            />
                        </div>
                    </React.Fragment>
                ) : (
                    <EnterChat
                        setToken={this.setToken.bind(this)}
                    />
                )}
            </div>
        )
    }
}

export default Chat;
