// The reducer for all chats

import * as actionType from '../constants/action-types';

const initialState = {
    chats: []
};

const chatReducer = (state = initialState, action) => {
    switch(action.type) {
        case actionType.GET_CHATS:
            return [
                ...state,
                ...action.payload
            ];

        default: 
            return state;
    }
}

export default chatReducer;