import React, {Component} from 'react';
import userImg from '../../../assets/images/user.svg'
import Moment from 'react-moment';

class MessageLoad extends Component {

    constructor(props){
        super(props);

        this.state = {
            messages : props.messagesLoad
        }

    }

    render() {
        let messasgesLoadList ='';
        if (this.state.messages.length) {
            messasgesLoadList = this.state.messages.slice(0).reverse().map(message => {

                let colorMessage = {
                    color: message.color
                };
                let colorBg = {
                    backgroundColor : message.color
                };
                let data = message.message.created_at;

                return (
                    <div className="message">
                        <div className="avatar" style={colorBg}>
                            <img src={message.message.member.avatar || userImg} />
                        </div>

                        <div className="data">
                            <div className="head-message">
                                <p className="username" style={colorMessage}>{message.message.member.username}</p>
                                <span className="time-label">
                                    <Moment title={data} parse="YYYY-MM-DD HH:mm" format="hh:mm"> {data} </Moment>
                                </span>
                            </div>
                            {message.message.type === 'text' ? (
                                <div className="text">
                                    {message.message.body}
                                </div>
                            ) : (
                                <div className="image">
                                    <img src={message.message.url} alt=""/>
                                </div>
                            )}
                        </div>
                    </div>
                )
            });
        }

        return (
            <div>
                {messasgesLoadList}
            </div>
        )
    }
};

export default MessageLoad;
