import React, { Component } from 'react';
import { Picker } from 'emoji-mart';
import uploadImg from '../../../assets/images/image.svg'
import uploadFile from '../../../assets/images/paperclip.svg'
import btnImage from '../../../assets/images/smile.svg'
import FocusLock from 'react-focus-lock';

class ChatMessageForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            messageText: '',
            caretPosition: 0,
            emoji: '',
            persistentFocus: false,
            isEmojiOpened: true,
        }

        this.inputMessage = React.createRef();
    }

    handleToggleEmojiPicker = () => {

        this.setState((prevState) => {
            return {
                isEmojiOpened: !prevState.isEmojiOpened
            }
        })
    }

    handleMessageText = event => {
        this.setState({
            messageText: event.target.value
        })
    }

    hendleSendmessage = (e) =>{
        if (e.key === 'Enter') {
            if(this.state.messageText.length) {
                this.props.sendMessage(this.state.messageText);
                this.setState({messageText : ''});
            }
        }
    }

    handleSelectEmoji = emoji => {
        console.log(emoji)
        this.insertTextToInput(this.inputMessage.current, emoji.native, this.state.messageText);
    }


    insertTextToInput(input, text, inputValueFromState) {

        let inputValue = inputValueFromState || input.value;


        if (input.selectionStart !== undefined) {
            input.focus();

            // Save a caret position of input
            const position = input.selectionStart;
            const messageStart = inputValue.substring(0, input.selectionStart);
            const messageEnd = inputValue.substring(input.selectionEnd, inputValue.length);

            // Concatenate the message's cuts with passed text
            inputValue = messageStart +  text + messageEnd;

            // Setup initial state for the caret
            // if you don't have to pass an emoji
            // remove an expression \- (text.length - 1)\
            // from the bellow string;
            input.selectionStart = position + text.length - (text.length - 1);
            input.selectionEnd = input.selectionStart;

            this.setState({
                messageText: inputValue,
                caretPosition: input.selectionStart
            })

        } else {
            inputValue += text
        }
    }

    componentDidUpdate(_prevProps, prevState) {
        if( prevState.caretPosition !== this.state.caretPosition ) {
            // Set the caretPosition to position after inserting of text
            this.inputMessage.current.selectionStart = this.state.caretPosition + 1;
            this.inputMessage.current.selectionEnd = this.inputMessage.current.selectionStart
        }
    }


    render() {
        const isFormPrivate = this.props.type === 'private';

        const formUploadMediaButtons = isFormPrivate ?
            (
                <div className="input-group-prepend">
                    <div className="inputContainer">
                        <input type="file" itemID="file" />
                        <label tabIndex="0" htmlFor="file" className="input-file-trigger"><img src={uploadImg} /> </label>
                    </div>
                    <div className="inputContainer">
                        <input type="file" itemID="image" />
                        <label tabIndex="0" htmlFor="image" className="input-file-trigger"><img src={uploadFile} /> </label>
                    </div>
                </div>
            )
            :
            null;

        return (

            <div
                className="chat-widget-message-container"
                onClick={this.handleInputMessageFocus}>

                {formUploadMediaButtons}

                <div className="form-col">
                    <FocusLock persistentFocus={this.state.persistentFocus}>
                        <input
                            ref={this.inputMessage}
                            placeholder="Write a message..."
                            value={this.state.messageText}
                            onChange={this.handleMessageText}
                            onKeyUp={this.hendleSendmessage}
                        />
                    </FocusLock>
                    <button onClick={this.handleToggleEmojiPicker}><img src={btnImage} /> </button>
                </div>
                {!this.state.isEmojiOpened && <Picker onSelect={this.handleSelectEmoji} />}
            </div>

        )
    }
};

export default ChatMessageForm;
